.toms-image-slider-preview{
    /* Slider Preview */
    .toms-image-slider-image{
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;

        .toms-image-slider-gellery-image{
            display: inline-block;
            width: 100%;
            height: auto;
           
            &:hover{
                cursor: pointer;
            }
        }
        .left-arrow{
            display: flex;
            justify-content: flex-start;
            color: #000;
            z-index: 10;
            user-select: none;
            margin-right: -50px;
            width: 32px;
            height: 32px;
            transition: transform 1s ease-out;
            fill:rgba(0, 0, 0, 0.3);

            svg:hover {
                fill:rgba(0, 0, 0, 0.7);
                transition-duration: 1s;
                cursor: pointer;
            }
        }
        .right-arrow{
            display: flex;
            justify-content: flex-end;
            color: #000;
            z-index: 10;
            user-select: none;
            width: 32px;
            height: 32px;
            margin-left: -50px;
            transition: transform 1s ease-out;
            fill:rgba(0, 0, 0, 0.3);

            svg:hover {
                fill:rgba(0, 0, 0, 0.7);
                transition-duration: 1s;
                cursor: pointer;
            }
        }
        
        .toms-image-slider-slide{
            opacity: 0;
            transition: transform 1s ease-out;
        }
        .toms-image-slider-slide-active{
            opacity: 1;
            transition-duration: 1s;
            transform: scale(1.08);
        }
    }
    .toms-image-slider-thumnbail{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        
        .toms-image-slider-thumnbail-image{
            width: 25%;
            height: auto;
            padding: 1px;

            &:hover{
                cursor: pointer;
            }
        }
        .toms-image-slider-gellery-thumnbail-image {
            display: block;
            width: 100%;
            height: auto;
            opacity: 0.7;
            -webkit-filter: blur(0.5px); /* Safari 6.0 - 9.0 */
            filter: blur(0.5px);

            &:hover{
                -webkit-filter: blur(0px); /* Safari 6.0 - 9.0 */
                filter: blur(0px);
            }
        }
        .toms-image-slider-gellery-thumnbail-image-active {
            opacity: 1;
            border: 1px rgb(158, 158, 158, 0.4) solid;
            -webkit-filter: blur(0px); /* Safari 6.0 - 9.0 */
            filter: blur(0px);
        }
        
    }
}
.toms-image-slider-modal-overlay{
    background-color: rgba(0,0,0,0.7);    
    width: 100vw;
    height: 100vh;
    z-index: 100000;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}
.toms-image-slider-modal-block{
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-color: transparent;
    background-position: center center;
    background-size: contain;

    .components-modal__content{
        padding: 0;

        .components-modal__header{
            display: flex;
            justify-content: flex-end;
            margin: 0;
            padding: 10px 20px 0 0;
            background: transparent;
            border: none;
            
            button{
                width: 48px;
                height: 48px;
                display: flex;
                justify-content: center;
                align-items: center;
                padding: 0;
                border-radius: 50%;
                background-color: rgb(255,0,0, 0.4);

                span{
                    display: none;
                    opacity: 0;
                }

                svg{
                    width: 48px;
                    height: 48px;
                    fill: rgb(255, 255, 255, 0.8);
                    transition: transform 0.3s ease-out;

                    &:hover{
                        transform: rotate(90deg);
                    }
                }

                &:hover{
                    transition-duration: 1s;
                    background-color: rgba(255, 0, 0, 0.7);
                    transform: scale(1.1);
                }
                &:focus{
                    outline: none;
                    box-shadow: none;
                }
            }
            .components-modal__header-heading-container{
                justify-content: center;
            }
        }
    }
    .toms-image-slider-modal-block{
        display: flex;
        flex: 1;
        box-sizing: inherit;
        flex-wrap: wrap;

        .toms-image-slider-modal-main{
            display: flex;
            flex: 1;
            justify-content: center;

            .toms-image-slider-modal{
                /*display: flex;*/
                flex: 1;
                /*justify-content: center;
                flex-direction: column;
                align-items: center;*/

                .toms-image-slider-modal{
                   /* display: flex;*/
                    flex: 1;
                    .toms-image-slider-modal-image{
                        display: block;
                        width: 100%;
                        height: auto;
                    }  
                    &:hover{
                        cursor: pointer;
                    }
                }
                /*
                @media only screen and (max-width: 600px) {
                    .toms-image-slider-modal {
                        width: 100%;
                    }
                }*/
            }
        }
        .toms-image-slider-modal-show-thumnbail-icon{
            position: absolute;
            top: 8px;
            right: 100px;
            z-index: 100001;
            fill:rgba(7, 241, 41, 0.8);

            svg{
                fill:rgba(7, 241, 41, 0.8);

                &:hover{
                    fill:rgba(0, 150, 37, 0.7);
                    transition-duration: 1s;
                    cursor: pointer;
                }
            }
        }
        .modal-left-arrow{
            flex: 1;
            top: 47%;
            position: absolute;
            left: 10px;
            padding: 0 0 0 10px;
            fill:rgba(0, 255, 37, 0.3);
            width: 64px;
            height: 64px;

            svg {
                fill:rgba(0, 255, 37, 0.3);
                &:hover {
                    fill:rgba(0, 255, 37, 0.7);
                    transition-duration: 1s;
                    cursor: pointer;
                }
            }
        }
        .modal-right-arrow{
            flex: 1;
            top: 47%;
            right: 10px;
            position: absolute;
            padding: 0 10px 0 0;
            fill:rgba(0, 255, 37, 0.3);
            width: 64px;
            height: 64px;

            svg {
                fill:rgba(0, 255, 37, 0.3);
                &:hover {
                    fill:rgba(0, 255, 37, 0.7);
                    transition-duration: 1s;
                    cursor: pointer;
                }
            }
        }
        .toms-image-slider-modal-thumnbail{
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            flex: 1;
            justify-content:center;
            width: 100%;
            position: fixed;
            bottom: 0;
            
            .toms-image-slider-modal-thumnbail-image{
                width: 12%;
                height: auto;
                padding: 1px;
    
                &:hover{
                    cursor: pointer;
                }
            }
            @media only screen and (max-width: 800px) {
                .toms-image-slider-modal-thumnbail-image{
                    width: 25%;
                }
            }
            .toms-image-slider-gellery-modal-thumnbail-image {
                width: 100%;
                height: auto;
                opacity: 0.7;
                -webkit-filter: blur(0.5px); /* Safari 6.0 - 9.0 */
                filter: blur(0.5px);

                &:hover{
                    -webkit-filter: blur(0px); /* Safari 6.0 - 9.0 */
                    filter: blur(0px);
                }
            }
            .toms-image-slider-gellery-modal-thumnbail-image-active {
                opacity: 1;
                border: 1px rgb(158, 158, 158, 0.4) solid;
                -webkit-filter: blur(0px); /* Safari 6.0 - 9.0 */
                filter: blur(0px);
            }
                
        }
    }
}